@use "../abstracts/mixins" as *;
.hero-newsletter-01{
    position: relative;
    .form-group{
        position: relative;
        .form-control{
          box-shadow: 0px 15px 35px rgba(210, 217, 224, 0.25);
          border-radius: 5px;
          padding: 0;
          font-weight: 500;
          font-size: 16px;
          line-height: 1.2;
          height: 60px;
          border: 0;
          padding: 0 20px 0 50px;
          color: var(--bs-black);
          @media (min-width:575px) {
              font-size: 18px;
              height: 71px;
              padding-left: 65px;
              padding-right: 200px;

          }
          &::placeholder{
              color: var(--bs-gray);
          }
        } 
        .icon{
            position: absolute;
            top: 48%;
            left: 20px;
            transform: translateY(-50%);
            @media (min-width:575px) {
                left: 30px;
            }
        }
    }
  button{
      width: 100%;
      margin-top: 20px;
      @media (min-width:575px) {
          width: auto;
        margin-top: 0px;
        position: absolute;
        top: 50%;
        right: 8px;
        transform: translateY(-50%);
    }
  } 
}
.search-newsletter-01{
    position: relative;
    .form-group{
        position: relative;
        .form-control{
            border-radius: 5px;
            padding: 0;
            font-weight: 500;
            font-size: 16px;
            line-height: 1.2;
            height: 55px;
            border: 0;
            background: var(--bs-white-lilac);
            padding: 0 20px 0 50px;
            color: var(--bs-black);
            @media (min-width:480px) {
                font-size: 18px;
                padding-left: 65px;
                padding-right: 200px;

            }
            &::placeholder{
                color: var(--bs-gray);
            }
        } 
        .icon{
            position: absolute;
            top: 48%;
            left: 20px;
            transform: translateY(-50%);
            @media (min-width:575px) {
                left: 30px;
            }
        }
    }
    button{
        width: 100%;
        margin-top: 20px;
        @media (min-width:480px) {
        width: auto;
        margin-top: 0px;
        position: absolute;
        top: 50%;
        height: 100%;
        right: 0;
        transform: translateY(-50%);
    }
    } 
}
.search-newsletter-02{
    position: relative;
    .form-group{
        position: relative;
        .form-control{
            border-radius: 5px;
            padding: 0;
            font-weight: 500;
            font-size: 16px;
            line-height: 1.2;
            height: 65px;
            border: 0;
            background: var(--bs-white-lilac);
            padding: 20px;
            color: var(--bs-black);
            @media (min-width:480px) {
                font-size: 18px;
                padding-right: 163px;
            }
            &::placeholder{
                color: var(--bs-gray);
            }
        } 
    }
    button{
        width: 100%;
        height: 55px;
        margin-top: 20px;
        @media (min-width:480px) {
        width: auto;
        margin-top: 0px;
        position: absolute;
        top: 50%;
        right: 5px;
        transform: translateY(-50%);
    }
    } 
}
.content-list-01{
    ul{
        li{
            display: flex;
            margin-bottom: 15px;
            @media (min-width:575px) {
                margin-bottom: 20px;
            }
            &:last-child{
                margin-bottom: 0;
            }
            span{
                margin-right: 12px;
                svg{
                    display: block;
                }
            }
            font-weight: 500;
            color: var(--bs-black);
            font-size: 16px;
            line-height: 1.2;
            @media (min-width:575px) {
                font-size: 18px;
            }
        }
    }
}
.about-wdidget-01{
    background: var(--bs-white);
    padding: 30px 25px;
    position: relative;
    border-radius: 3px;
    overflow: hidden;
    transition: .3s;
    display: flex;
    align-items: flex-start;
    @media (max-width:575px) {
        flex-direction: column;
    }
    @media (min-width:992px) {
        padding: 40px;
    }
    &::before{
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        visibility: hidden;
        transition: .3s;
        opacity: 0;
        content: "";
        height: 2.53px;
        background: var(--bs-primary);
    }
    .widget-icon{
        @media (max-width:575px) {
            margin-bottom: 20px;
        }
        @media (min-width:575px) {
            margin-right: 30px;
        }
    }
    .wdidget-content{
        h6{
            font-weight: 600;
            font-size: 20px;
            line-height: 1.2;
            margin-bottom: 15px;
            @media (min-width:992px) {
                margin-bottom: 20px;
                font-size: 24px;
            }
        }
        p{
            font-weight: 500;
            color: var(--bs-gray);
            font-size: 16px;
            line-height: 1.555555555555556;
            @media (min-width:575px) {
                font-size: 18px;
            } 
        }
    }
    &:hover{
        box-shadow: 0px 15px 30px rgba(210, 217, 224, 0.35);
        &::before{
            opacity: 1;
            visibility: visible;
        }
    }
}
.content-list-02{
    .content-list-wrapper{
        display: flex;
        align-items: flex-start;
        .list{
            margin-right: 20px;
            transform: translateY(-9px);
            @include square(45px,500px);
            @include flexAllCenter;
            background: var(--bs-primary);
            font-weight: 600;
            color: white;
            font-size: 18px;
            @media (min-width:575px) {
                font-size: 20px;
                margin-right: 25px;
            }
            @media (min-width:992px) {
                font-size: 24px;
            }
        }
        .content{
            h6{
                font-weight: 600;
                font-size: 18px;
                margin-bottom: 20px;
                color: var(--bs-dark);
                line-height: 1.2;
                @media (min-width:575px) {
                    font-size: 20px;
                }
                @media (min-width:992px) {
                    font-size: 24px;
                }
            }
            p{
                font-weight: 500;
                color: var(--bs-gray);
                line-height: 1.555555555555556;
                font-size: 16px;
                @media (min-width:575px) {
                    font-size: 18px;
                }
            }
        }
    }
}
.content-list-03{
    .short-title{
        font-weight: 600;
        font-size: 20px;
        line-height: 1.2;
        padding: 0;
        color: var(--bs-black);
        margin-bottom: 20px;
        @media (min-width:575px) {
            font-size: 24px;
            margin-bottom: 30px;
        }
    }
    ul{
        margin: 0;
        padding: 0;
        list-style: none;
        li{
            display: flex;
            padding: 11px 0;
            font-weight: 500;
            margin: 0;
            line-height: 1.2;
            font-size: 16px;
            color: var(--bs-gray);
            @media (min-width:575px) {
                font-size: 18px;
            }
            span{
                margin-right: 12px;
                display: inline-block;
                svg{
                    display: block;
                }
            }
            &:last-child{
                padding-bottom: 0;
            }
            &:first-child{
                padding-top: 0;
            }
        }
    }
}
.about-wdidget-02{
    .short-title{
        font-weight: 600;
        font-size: 20px;
        line-height: 1.2;
        padding: 0;
        color: var(--bs-black);
        margin-bottom: 10px;
        @media (min-width:575px) {
            font-size: 24px;
            margin-bottom: 20px;
        }
    }
    ul{
        margin: 0;
        padding: 0;
        display: grid;
        grid-template-columns: repeat(2, minmax(1, 50%));
        gap: 0px 40px;
        padding: 0px;
        overflow: hidden;
        list-style: none;
        @media (min-width:575px) {
            grid-template-columns: repeat(2, minmax(0, 50%));
        }
        li{
            display: flex;
            padding: 11px 0;
            font-weight: 500;
            line-height: 1.2;
            font-size: 16px;
            color: var(--bs-gray);
            @media (min-width:575px) {
                font-size: 18px;
            }
            span{
                margin-right: 12px;
                display: inline-block;
                svg{
                    display: block;
                }
            }
        }
    }
}
.content-list-04{
    display: flex;
    align-items: flex-start;
    .list{
        @include square(45px,3px);
        @include flexAllCenter;
        background: var(--bs-solitude);
        font-weight: 600;
        color: var(--bs-primary);
        font-size: 18px;
        margin-right: 20px;
        @media (min-width:575px) {
