@use "../abstracts/typography"as *;

.page-title-block {
  .title {
    font-weight: 600;
    line-height: 1.225;
    font-size: 52px;
    @media (min-width: 992px) {
      font-size: 60px;
    }
    @media (min-width: 1200px) {
      font-size: 70px;
    }
    @media (min-width: 1400px) {
      font-size: 80px;
    }
  }
  p{
    font-weight: 500;
    font-size: 18px;
    line-height: 1.555555555555556;
  }
}
.page-title-block-two {
  .subtitle{
    font-size: 16px;
    font-weight: 600;
    line-height: 1.2;
    @media (min-width: 575px) {
      font-size: 18px;
    }
  }
  .title {
    font-weight: 600;
    line-height: 1.310344827586207;
    font-size: 42px;
    @media (min-width: 768px) {
      font-size: 52px;
    }
    @media (min-width: 1200px) {
      font-size: 58px;
    }
  }
  p{
    font-weight: 500;
    font-size: 16px;
    line-height: 1.555555555555556;
    @media (min-width: 575px) {
      font-size: 18px;
    }
  }
}

.section-title-block,
.section-content-block {
  .subtitle{
    font-size: 18px;
    font-weight: 600;
    line-height: 1.2;
    text-transform: uppercase;
  }
  .title {
    font-weight: 600;
    line-height: 1.35;
    font-size: 32px;
    @media (min-width: 768px) {
      font-size: 40px;
    }
  }
  p {
    font-size: 16px;
    font-weight: 500;
    line-height: 1.555555555555556;
    @media (min-width: 575px) {
      font-size: 18px;
    }
  }
}
.content-title{
  font-weight: 600;
  stroke-linejoin: 1.302916666666667;
  font-size: 18px;
  @media (min-width:575px) {
      font-size: 24px;
  }
}

.font-size-30{
  font-size: 24px;
  font-weight: 600;
  line-height: 1.4;
  @media (min-width:575px) {
    font-size: 28px;
  }
  @media (min-width:992px) {
    font-size: 30px;
  }
}
